<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="name">现金流汇总数据</span>
				</div>
	
			</div>
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text"   ></el-table-column>
				</el-table>
			</div>
		</div>
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="name">现金流数据</span>
					<el-select v-model="cashFlowType" placeholder="类型" class=" select_border" style="width:140px;margin-left:30px;" clearable @change="typeChangeGetData()">
						<el-option v-for="item in typeList" :key="item.type" :label="item.type_name" :value="item.type">
						</el-option>
					</el-select>
					<div class="search_box ">
						<el-input v-model="cashFlowProject" prefix-icon="el-icon-search" placeholder="事件名称" clearable>
						</el-input>
					</div>
					<div class="search_box">
						<div class="btn_box">
							<button @click="toSearch()">搜索</button>
						</div>
					</div>
				</div>
			</div>
			
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="tableData1" border style="width: 100%" max-height="350">
					<el-table-column prop="index" label="序号" width="80">
					</el-table-column>
					<el-table-column sortable prop="date" label="日期" width="140">
					</el-table-column>
					<el-table-column sortable prop="outside_type_name" label="类型" width="120">
					</el-table-column>
					<el-table-column sortable prop="event" label="事件" width="250">
					</el-table-column>
					<el-table-column sortable prop="fee" label="金额" width="180">
					</el-table-column>
					<el-table-column sortable prop="should_fee" label="应计金额" width="180">
					</el-table-column>
					<el-table-column sortable prop="unitValue" label="币种" width="195">
					</el-table-column>
					<el-table-column sortable prop="note" label="备注" width="195">
					</el-table-column>
					
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="scope">
							<el-button @click="toCashFlowPage(scope.row)" type="text" size="small">查看</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<el-pagination v-if="total>0" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="page" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    			</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import {dialogConfirm,dialogConfirmHasbtn} from '../../config/mUtils'
	import {} from '../../config/api'
	import {SummarizingInfoChildFundEvents,SummarizingInfoChilfFund,} from '../../config/apiSj'
	export default {
		data() {
			return {
				id: '',//母基金/自管基金ID
				
				//筛选列表
				typeList: [
				],//筛选类型下拉列表

				//分页
				total: 0,
				page: 1,
				pageSize: 10,

				//现金流列表
				cashFlowType: '', //现金流类型
				cashFlowProject: '', //现金流类型项目
				cashFlowDropboxShow:false,//是否下拉
				
				//表格头部，数据
				tableHeader:[
					{
						param:'unitValue',
						text:'币种',
						width:'100',
					},{
						param:'subscribe',
						text:'缴款(万元)',
						width:'120',
					},{
						param:'sum',
						text:'分配总额(万元)',
						width:'120',
					},{
						param:'sum_fee',
						text:'分配本金(万元)',
						width:'160',
					},{
						param:'sum_fee_after_tax',
						text:'分配收益-税后(万元)',
						width:'120',
					},
				],
				tableData: [],
				tableData1: [],
				type:'',
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getListSummaryData();
			this.getListCashFlowData();
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			
			//顶部导航栏下拉框打开关闭
			DropboxShow() {
				this.cashFlowDropboxShow = !this.cashFlowDropboxShow;
			},
			//跳转添加页面
			// toAdd(type){
			// 	let pageName = this.selectCashFlowType(type);
			// 	this.$router.push({
			// 		name: pageName,
			// 		query: {
			// 			id: this.id,
			// 			is_add_page: 1,
			// 			type: this.type,
			// 		}
			// 	})
			// },
			//获取对应类型页面路由
			// selectCashFlowType(type){
			// 	let pageName = '';
			// 	switch(type){
			// 		case 1:
			// 			pageName = 'motherFunRunDataCashFlowJk';
			// 			break;
			// 		case 2:
			// 			pageName = 'motherFunRunDataCashFlowInvest';
			// 			break;
			// 		case 3:
			// 			pageName = 'motherFunRunDataCashFlowBackMoney';
			// 			break;
			// 		case 4:
			// 			pageName = 'motherFunRunDataCashFlowFpEdit';
			// 			break;
			// 		case 5:
			// 			pageName = 'motherFunRunDataCashFlowPay';
			// 			break;
			// 		case 6:
			// 			pageName = 'motherFunRunDataCashFlowIncom';
			// 			break;
					
			// 	}
			// 	return pageName;
			// },
			
			//跳转详情页面
			toCashFlowPage(row){
				this.$router.push({
					name: 'subFundRunCashListDetail',
					query: {
						id: this.id,
						count:row.count,
						type:row.inside_type,
						cash_type:row.outside_type,
					}
				})
			},

			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListCashFlowData()
			},
			changeCurrentPage(e){
				this.page = e;
				this.getListCashFlowData()
			},
			//搜索条件修改获取数据
			typeChangeGetData(){
				this.cashFlowProject = '';
				this.page = 1;
				this.getListCashFlowData();
			},
			//去搜索
			toSearch(){
				this.page = 1;
				this.getListCashFlowData();
			},

			//获取现金流汇总数据
			getListSummaryData(){
				SummarizingInfoChilfFund({
					childfund_id:this.id,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload.data;
						data.forEach(element => {
							if(element.unit==1){
								element.unitValue = '人民币';
							}else{
								element.unitValue = '美元';
							}
						});
						this.tableData = data;
					}
				})
			},
			//获取现金流数据
			getListCashFlowData(){
				SummarizingInfoChildFundEvents({
					page: this.page,
					size:this.pageSize,

					type:this.cashFlowType,
					search_content:this.cashFlowProject,
					childfund_id: this.id,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						if(data.data.length>0){
							data.data.forEach((element,index) => {
								element.index = (this.page-1)*this.pageSize+index+1;
								if(element.unit==1){
									element.fee = element.fee ? element.fee + '万人民币' : '-' ;
									element.should_fee = element.should_fee ? element.should_fee + '万人民币' : '-';
									element.unitValue = '人民币';
								}else{
									element.fee = element.fee ? element.fee + '万美元' : '-' ;
									element.should_fee = element.should_fee ? element.should_fee + '万美元' : '-';
									element.unitValue = '美元';
								}
							});
						}
						this.total = data.count;
						this.tableData1 = data.data;
						this.typeList = data.types;
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}

	.flex_start_right{
		position: relative;
	}
	.cash_flow_dropbox {
		position: absolute;
		border: 1px solid #E3EAEF;
		visibility: hidden;
		transition: all 0.3s;
		top: 30px;
		left: 50%;
		transform: translateX(calc(-50% + 7px));
		opacity: 0;
		box-shadow: 0px 0px 4px 2px rgba(128, 128, 128, 0.1);
		border-radius: 10px;
		z-index: 100;
	}

	.cash_flow_dropbo_show {
		visibility: visible;
		opacity: 1;
		top: 40px;
		left: 50%;
  		transform: translateX(calc(-50% + 7px));
	}
	.cash_flow_dropbox_item {
		width: 180px;
		height: 36px;
		font-size: 14px;
		background: #fff;
		color: #545454;
		position: relative;
		padding-left: 30px;
		&:hover {
			background: #F3F6FB;
			cursor: pointer;
		}
		.iconfont{
			font-size: 16px;
			margin-right: 15px;
		}
		.icon-jiaokuan{
			color: #72D39D;
		}
		.icon-touzi{
			color: #ED7B5C;
		}
		.icon-huikuan{
			color: #5AC8FA;
		}
		.icon-fenpei{
			color: #6292F4;
		}
		.icon-shouru{
			color: #5AC8FA;
		}
		.icon-zhichu{
			color: #6292F4;
		}
	}
	.cash_flow_dropbox_item1{
		background-color: #fff;
		height: 15px;
		width: 180px;
	}


	.search_box{
		width: 150px;
		height: 30px;
		margin-left: 13px;
	}
	
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
</style>


